export const getMovieFetch = () => fetch('http://localhost:1488/movies').then(response => !response.ok ? console.error(`Error get movies, status code ${response.status}`) : response.json());
export const delMovieFetch = (id) => fetch(`http://localhost:1488/movies/${id}`, { method: "DELETE" }).then(response => !response.ok ? console.error(`Error del movie, status code ${response.status}`) : response.json());
export const addMovieFetch = (newMovie) => fetch(`http://localhost:1488/movies/`, { method: "POST", body: JSON.stringify(newMovie) }).then(response => !response.ok ? console.error(`Error add movie, status code ${response.status}`) : response.json())
export const updateMovieFetch = (id, updateMovie) => fetch(`http://localhost:1488/movies/${id}`, { method: "PUT", body: JSON.stringify(updateMovie) }).then(response => !response.ok ? console.error(`Error update movie, status code ${response.status}`) : response.json());
export const editMovieFetch = (id, editMovie) => fetch(`http://localhost:1488/movies/${id}`, { method: "PATCH", body: JSON.stringify(editMovie) }).then(response => !response.ok ? console.error(`Error edit movie, status code ${response.status}`) : response.json())